import { createSlice, isAnyOf } from '@reduxjs/toolkit';
//   Thunk- fetchContacts, addContacts, deleteContacts   './operations'
import { fetchContacts, addContacts, deleteContacts } from './operations';

//   extraActions,    Thunk-
// const extraActions = [fetchContacts, addContacts, deleteContacts];

//   getActions,    isAnyOf    
// const getActions = type => isAnyOf(...extraActions.map(action => action[type]));

//   getActions,    isAnyOf    
const getActions = type =>
  isAnyOf(fetchContacts[type], addContacts[type], deleteContacts[type]);

//    slice contactsSlice
const initialState = { items: [], isLoading: false, error: null };

//  slice   
const contactsSlice = createSlice({
  name: 'contacts', //  '  slice
  initialState, //   slice
  extraReducers: builder =>
    builder
      .addCase(fetchContacts.fulfilled, (state, action) => {
        //    fetchContacts
        state.items = action.payload; //     
      })
      .addCase(addContacts.fulfilled, (state, action) => {
        //    addContacts
        state.items.unshift(action.payload); //        
      })
      .addCase(deleteContacts.fulfilled, (state, action) => {
        //    deleteContacts
        const index = state.items.findIndex(
          contact => contact.id === action.payload.id
        );
        state.items.splice(index, 1); //     
      })
      .addMatcher(getActions('pending'), state => {
        //     'pending' 
        state.isLoading = true; //   isLoading true
      })
      .addMatcher(getActions('rejected'), (state, action) => {
        //     'rejected' 
        state.isLoading = false; //   isLoading false
        state.error = action.payload; //    
      })
      .addMatcher(getActions('fulfilled'), state => {
        //     'fulfilled' 
        state.isLoading = false; //   isLoading false
        state.error = null; //     null
      }),
});

//   addContact  deleteContact  slice contactsSlice
export const { addContact, deleteContact } = contactsSlice.actions;

//   (reducer) contactsReducer  slice contactsSlice
export const contactsReducer = contactsSlice.reducer;
